import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { UserModel } from '../users/user.model';
import { TokenService } from '../token/token.service';
import { UserService } from '../users/user.service';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { CurrentUser } from '../token/current-user.model';

declare var $:any;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  user = new UserModel(null,'','', '', false, '', '', '', false);
  token : TokenService;
  message : string;
  formLogin : FormGroup;

  constructor( 
              private userservice: UserService, 
              private router: Router,
              private formBuilder: FormBuilder) {
                this.token = TokenService.getInstance();
               }

  ngOnInit() {

    this.formLogin = this.formBuilder.group({
      nameUser: this.formBuilder.control('', [Validators.required]),
      password: this.formBuilder.control('', [Validators.required])
    })
  }

  login(){
    this.user.login = this.formLogin.get('nameUser').value;
    this.user.password = this.formLogin.get('password').value;
    this.userservice.login(this.user).subscribe((userAuthentication: CurrentUser) => {
      this.token.token = userAuthentication.token;
      this.token.user = userAuthentication.users;
      this.token.user.profileEnum = this.token.user.profileEnum.substring(5);
      this.token.showTemplate.emit(true);
      this.router.navigate(['/']);
    }, err => {
      this.token.token = null;
      this.token.user = null;
      this.token.showTemplate.emit(false);
      this.showNotification('Desculpe, mas o login ou senha invlidos!', 'danger', 'ti-face-sad')
    });
  }

  showNotification(message: string, type: string, icon: string) {
    $.notify({
      icon: icon,
      message: message
    }, {
        type: type,
        timer: 1500,
        placement: {
          from: "top",
          align: 'center',
        }
      });
  }

}
