import { Component, OnInit } from '@angular/core';
import { LaunchesService } from './launches.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { LaunchesModel } from './launches.model';
import { ProviderService } from '../provider/provider.service';
import { CategoryService } from '../category/category.service';
import { ProviderModel } from '../provider/provider.model';
import { SubCategoryModel } from '../category/sub-category.model';
import { AccountModel } from '../account/account.model';
import { AccountService } from '../account/account.service';
import createNumberMask from 'text-mask-addons/dist/createNumberMask';
import { HttpEventType, HttpResponse } from '@angular/common/http';
import { PredictionService } from 'app/prediction/prediction.service';


declare var $: any;

@Component({
  selector: 'app-launches',
  templateUrl: './launches.component.html',
})
export class LaunchesComponent implements OnInit {

  formCadLaunches: FormGroup;

  providers: ProviderModel[];

  subCategories: SubCategoryModel[];

  subCatFiltered: SubCategoryModel[] = [];

  accounts: AccountModel[];

  flag: number = 1;

  id: number;

  vcto: Date;

  percentage: number = 0;

  stringPercent: string = this.percentage + "%";

  receptiveFile: File = null;

  launcheChange = new LaunchesModel(0, null, null, null, null, null, '', '', 0, 0, 0, 0, 0);

  constructor(private launcheService: LaunchesService, private formBuilder: FormBuilder, private providerService: ProviderService,
    private categoryServie: CategoryService, private predictionService: PredictionService, private accountService: AccountService) { }

  ngOnInit() {

    this.getAllProviders();

    this.getAllSubCategories();

    this.getAllAccounts();

    this.formCadLaunches = this.formBuilder.group({
      dt: this.formBuilder.control('', [Validators.required]),
      provider: this.formBuilder.group({
        id_provider: '',
      }),
      account: this.formBuilder.group({
        id_account: this.formBuilder.control('', [Validators.required]),
      }),
      subCategory: this.formBuilder.group({
        id_subCategory: this.formBuilder.control('', [Validators.required]),
      }),
      typeOfLaunch: this.formBuilder.control('S', [Validators.required]),
      description: this.formBuilder.control('', [Validators.required]),
      discont: this.formBuilder.control(0, [Validators.required]),
      interest: this.formBuilder.control(0, [Validators.required]),
      tt: this.formBuilder.control(0, [Validators.required]),
      pay_value: this.formBuilder.control('', [Validators.required]),
    });

    this.changeFlag();
  }

  saveLaunches(launches: LaunchesModel) {
    launches.balance = 0;
    if (this.flag === 1) {
      // console.log(this.findProviderById(launches.provider).name_provider)
      // launches.provider = this.findProviderById(launches.provider)
      this.launcheService.saveLaunche(launches)
        .subscribe((launche: LaunchesModel) => {
          this.showNotification("Lanamento efetuado com sucesso", "success", "ti-face-smile")
          this.formCadLaunches.reset()
          this.percentage = 0;
        })

    } else if(this.flag === 2) {
      this.launcheService.saveLaunche(launches)
        .subscribe((launche: LaunchesModel) => {
          this.showNotification("Lanamento efetuado 2 com sucesso", "success", "ti-face-smile")
          this.formCadLaunches.reset()
          this.percentage = 0;
          this.launcheService.setForIsPay();
          this.predictionService.putLaunches(this.launcheService.getLaunchPrediction()).subscribe((response) => { 
            this.launcheService.setLaunchPrediction(null);
            this.flag = 1;
          });
          
        })
    }
    
    else {
      launches.id_launch = this.id;
      this.launcheService.putLaunches(launches)
        .subscribe((launche: LaunchesModel) => {
          this.showNotification("Lanamento modificado com sucesso.", "success", "ti-face-smile")
          this.formCadLaunches.reset()
          this.flag = 1
          this.percentage = 0;
        })
    }

  }

  changeFlag(): void {
    if (this.launcheService.getLaunc() != null) {
      if (this.launcheService.getLaunchPrediction() == null){
        this.flag = 0;
      } else {
        this.flag = 2;
      }
      this.launcheChange = this.launcheService.getLaunc();
      var date2 = new Date(this.launcheChange.dt)
      console.log(date2);
      this.id = this.launcheChange.id_launch;
      this.formCadLaunches.setValue({
        dt: date2.toISOString().substring(0, 10),
        provider: {
          id_provider: this.launcheChange.provider.id_provider,
        },
        account: {
          id_account: this.launcheChange.account.id_account,
        },
        subCategory: {
          id_subCategory: this.launcheChange.subCategory.id_subCategory,
        },
        typeOfLaunch: this.launcheChange.typeOfLaunch,
        description: this.launcheChange.description,
        discont: this.launcheChange.discont,
        interest: this.launcheChange.interest,
        tt: this.launcheChange.interest,
        pay_value: this.launcheChange.pay_value,
      })
    }
    this.launcheService.setAccountPass(null);
  }

  findProviderById(idProvider: number) {
    console.log(idProvider)
    for (var i = 0; i < this.providers.length; i++) {
      if (this.providers[i].id_provider == idProvider) {
        console.log(this.providers[i])
        return this.providers[i];
      }
    }
  }

  onFileChange(event) {
    if (event.target.files[0].size > 10000000) {
      this.percentage = 0;
      this.showNotification("O arquivo tem mais de 10mb", "alert alert-warning", "ti-info-alt");
    } else {
      this.receptiveFile = event.target.files[0];
      var tipo: string[] = this.receptiveFile.type.split("/");
      if (tipo[tipo.length - 1] === "pdf" || (tipo[tipo.length - 1] === "jpg" || tipo[tipo.length - 1] === "jpeg")) {
        this.launcheService.upLoadFile(this.receptiveFile).subscribe(event => {
          if (event.type === HttpEventType.UploadProgress) {
            this.percentage = Math.round(100 * event.loaded / event.total);
            this.stringPercent = this.percentage + "%";
            console.log(this.percentage);
          } else {
            this.stringPercent = "Upload finalizado!"
          }
        });
      } else {
        this.percentage = 0;
        this.showNotification("Formato de arquivo no suportado. Formatos suportados: PDF, JPG, JPEG", "alert alert-warning", "ti-info-alt")
      }
    }
    console.log(this.receptiveFile);
  }

  getAllProviders() {
    this.providerService.getAllProviders().subscribe(providers => this.providers = providers)
  }

  getAllSubCategories() {
    this.categoryServie.getAllSubCategory().subscribe(subCategories => this.subCategories = subCategories)
  }

  getAllAccounts() {
    this.accountService.getAllAccounts().subscribe(accounts => this.accounts = accounts)
  }

  resetForm() {
    this.percentage = 0;
    this.formCadLaunches.reset();
  }

  getPercente() {
    return this.stringPercent;
  }

  deleteUpload() {
    this.launcheService.clearUpload();
      this.showNotification('Anexo excludo', "alert alert-warning", "ti-info-alt");
      this.percentage = 0;
      this.receptiveFile = null;
  }

  subCatFilter(filter: string) {
    this.subCatFiltered = [];
    this.subCategories.forEach(element => {
      if (element.category.typeOfLaunch === filter){
        this.subCatFiltered.push(element)
      }
    });
  }

  showNotification(message: string, type: string, icon: string) {
    $.notify({
      icon: icon,
      message: message
    }, {
        type: type,
        timer: 1500,
        placement: {
          from: "top",
          align: 'center',
        }
      });
  }
}
