import { ErrorHandler, Injectable } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';

declare var $: any;

@Injectable()
export class ApplicationErrorhandler extends ErrorHandler {

    constructor(){
        super()
    }

    handleError(errorResponse: HttpErrorResponse | any) {
        if (errorResponse instanceof HttpErrorResponse){
            switch (errorResponse.status){
                case 401:
                this.showNotification('Voc no est logado', 'alert alert-danger', 'ti-info-alt');
                break;
                case 403:
                this.showNotification('Voc no tem permisso de acesso.', 'alert alert-danger', 'ti-info-alt');
                break;
                case 404:
                this.showNotification('Falta de comunicao com o servidor', 'alert alert-danger', 'ti-info-alt');
                break;
            }
        }
        super.handleError(errorResponse);
    }

    showNotification(message: string, type: string, icon: string) {
        $.notify({
          icon: icon,
          message: message
        }, {
            type: type,
            timer: 1500,
            placement: {
              from: "top",
              align: 'center',
            }
          });
      }
}