import { Component, OnInit } from '@angular/core';
import { PrevisonService } from './prevision.service';
import { ProviderModel } from '../provider/provider.model';
import { ProviderService } from '../provider/provider.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { PredictionModel } from 'app/prediction/prediction.model';
import { PredictionPassModel } from 'app/prediction/prediction.pass.model';
import { PredictionService } from 'app/prediction/prediction.service';
import { Router } from '@angular/router';
import { LaunchesModel } from 'app/launches/launches.model';
import { LaunchesService } from 'app/launches/launches.service';

declare var $:any;

@Component({
  selector: 'app-prevision',
  templateUrl: './prevision.component.html',
  styleUrls: ['./prevision.component.css']
})
export class PrevisionComponent implements OnInit {

  providers: ProviderModel[];

  formPassFilter: FormGroup;

  predictions: PredictionModel[] = [];

  sumEntrada: number = 0;

  sumSaida: number = 0;


  constructor( private previsionService: PrevisonService, private providerService: ProviderService,
              private formBuilder: FormBuilder, private predictionService: PredictionService, 
              private router: Router, private launcheService: LaunchesService) { }

  ngOnInit() {

    this.formPassFilter = this.formBuilder.group({
      isPay: 0,
      periodo: this.formBuilder.control('0', [Validators.required]),
      initDate: '',
      finalDate: '',
    })

    this.getStartPredictions();
  }

  getStartPredictions(){
    this.previsionService.init()
      .subscribe((pro: PredictionModel[]) => {
        this.predictions = pro;
        this.calcSums();
      });
  }

  passFilter(filter: PredictionPassModel){
    this.previsionService.getAllPredictionByFilter(filter)
      .subscribe((elements: PredictionModel[]) => {
        this.predictions = elements;
        this.calcSums();
      });
  }

  predictionsIsEmpty(){
    if (this.predictions.length == 0){
      return true;
    }
    else{
      return false;
    }
  }

  deletePredictio(id: number){
    this.predictionService.deletePreLaunch(id).subscribe((response: PredictionModel) =>{
    this.getStartPredictions();
    this.showNotification('Programao excluda com sucesso',  "success", "ti-face-smile")
    })
  }

  changePrevision(prevision: PredictionModel){
    this.predictionService.setLaunch(prevision);
    this.router.navigateByUrl('/prediction');
  }

  calcSums(): void{
    this.sumEntrada = 0;
    this.sumSaida = 0;
    for (let i=0; i< this.predictions.length; i++){
      if(this.predictions[i].typeOfLaunch == "S"){
        this.sumSaida = this.sumSaida+this.predictions[i].value;
      } else {
        this.sumEntrada = this.sumEntrada+this.predictions[i].value;
      }
    }
  }

  payPrevision(prevision: PredictionModel){
    var date: Date = new Date;
    var launche = new LaunchesModel ( null, date, prevision.provider,
       prevision.account, prevision.subCategory, null, prevision.typeOfLaunch,
        prevision.description, 0, 0, 0, prevision.value, 0);
    this.launcheService.setAccountPass(launche);
    this.launcheService.setLaunchPrediction(prevision);
    this.router.navigateByUrl('/launches');
  }

  showNotification(message: string, type: string, icon: string) {
    $.notify({
      icon: icon,
      message: message
    }, {
        type: type,
        timer: 1500,
        placement: {
          from: "top",
          align: 'center',
        }
      });
  }

}
