import { Injectable } from "@angular/core";
import { LaunchesModel } from "./launches.model";
import { HttpClient, HttpRequest } from "@angular/common/http";
import { Http } from "@angular/http";
import { Observable } from "rxjs";
import { PF_API } from "pf.api";
import { PredictionModel } from "app/prediction/prediction.model";
import { TransferModel } from 'app/trasnfer/transfer.model';

@Injectable()
export class LaunchesService{

    accountpass: LaunchesModel;

    launchePut: LaunchesModel;

    launchePrediction: PredictionModel;

    constructor(
        private http: HttpClient,
    ){}

    saveLaunche(launches: LaunchesModel): Observable<LaunchesModel>{
        return this.http.post<LaunchesModel>(`${PF_API}/launches/cadastrar`, launches);
    }

    transfer(transfer: TransferModel): Observable<any>{
        return this.http.post<any>(`${PF_API}/launches/transferencia`, transfer);
    }

    getAllLaunches(search?: string): Observable<LaunchesModel[]>{
        return this.http.get<LaunchesModel[]>(`${PF_API}/launches/all`, {params: {q: search}});
    }

    putLaunches(launches: LaunchesModel): Observable<LaunchesModel>{
        return this.http.put<LaunchesModel>(`${PF_API}/launches/change`, launches);
    }

    deleteLaunch(idLaunche: number): Observable<LaunchesModel>{
        return this.http.get<LaunchesModel>(`${PF_API}/launches/delete/${idLaunche}`, )
    }

    getLaunc(): LaunchesModel{
        return this.launchePut;
    }

    setAccountPass(launche: LaunchesModel): void{
        this.launchePut = launche;
    }

    upLoadFile(file: File) {
        let formData = new FormData();
        formData.append('file', file);
        const req = new HttpRequest('POST', `${PF_API}/launches/upload2`, formData,{
            reportProgress: true,
            responseType: 'text'
        });
        return this.http.request(req);
    }

    clearUpload():Observable<any>{
        return this.http.get<any>(`${PF_API}/launches/clearupload`, )
    }

    setLaunchPrediction(prediction: PredictionModel){
        this.launchePrediction = prediction;
    }

    getLaunchPrediction(): PredictionModel{
        return this.launchePrediction;
    }

    setForIsPay(): void {
        this.launchePrediction.isPay = 1;
    }
}