import { Injectable, ErrorHandler } from "@angular/core";
import {Headers, RequestOptions} from '@angular/http';
import { UserModel } from "./user.model";
import { Observable } from "rxjs";
import { HttpClient } from '@angular/common/http';
import { PF_API } from "pf.api";
import 'rxjs/add/operator/map'
import 'rxjs/add/operator/catch'
import { CurrentUser } from "../token/current-user.model";

@Injectable()
export class UserService{
    constructor(
        private httpC: HttpClient,
    ){}

    saveUsers(usr: UserModel): Observable<UserModel>{
        return this.httpC.post<UserModel>(`${PF_API}/users/cadastrar`, usr);
    }

    saveNewUsers(usr: UserModel): Observable<UserModel>{
        return this.httpC.post<UserModel>(`${PF_API}/users/cadnewuser`, usr);
    }

    getAllUsers(search?: string): Observable<UserModel[]>{
        return this.httpC.get<UserModel[]>(`${PF_API}/users/allsimple`);
    }

    putUsers(usr: UserModel): Observable<UserModel>{
        return this.httpC.put<UserModel>(`${PF_API}/users/change`, usr);
    }

    login(user: UserModel):Observable<CurrentUser>{
        return this.httpC.post<CurrentUser>(`${PF_API}/api/auth`, user);
    }

    putUserFromCustomer(usr: UserModel): Observable<UserModel>{
        return this.httpC.put<UserModel>(`${PF_API}/users/changeuserbycustomer`, usr)
    }
}