import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Http } from "@angular/http";
import { LaunchesModel } from "../launches/launches.model";
import { Observable } from "rxjs";
import { PF_API } from "pf.api";
import { AccountModel } from "../account/account.model";
import { ExtractFilterModel } from "./extract.filter.model";

@Injectable()
export class ExtractService{

    
    constructor(
        private httpC: HttpClient,
    ){}

    getIt(): Observable<LaunchesModel[]>{
        return this.httpC.get<LaunchesModel[]>(`${PF_API}/extract/getIn`);
    }

    getPrincipalAccount(): Observable <AccountModel>{
        return this.httpC.get<AccountModel>(`${PF_API}/extract/get_principal`);
    }

    postFilter(filter: ExtractFilterModel): Observable<LaunchesModel[]>{
        return this.httpC.post<LaunchesModel[]>(`${PF_API}/extract/filter`, filter);
    }

    getFile(id: string){
        const httpOptions = {
            'responseType'  : 'arraybuffer' as 'json'
          };
        return this.httpC.get<any>(`${PF_API}/launches/file/${id}`, httpOptions);
    }
}