import { Injectable } from "@angular/core";
import { HttpClient } from '@angular/common/http';
import { Observable } from "rxjs";
import { PF_API } from "pf.api";
import { AccountModel } from "./account.model";

@Injectable()
export class AccountService{

    public accountpass: AccountModel;

    constructor(
        private http: HttpClient,
    ){}

    saveAccount(acc: AccountModel): Observable<AccountModel>{
        return this.http.post<AccountModel>(`${PF_API}/account/cadastrar`, acc);
    }

    getAllAccounts(search?: string): Observable<AccountModel[]>{
        return this.http.get<AccountModel[]>(`${PF_API}/account/all`);
    }

    putAccount(acc: AccountModel): Observable<AccountModel>{
        return this.http.put<AccountModel>(`${PF_API}/account/change`, acc);
    }

    getPrincipalAccount(): Observable<AccountModel> {
        return this.http.get<AccountModel>(`${PF_API}/account/principal`);
    }
}