import { Injectable } from "@angular/core";
import { HttpClient } from '@angular/common/http';
import { TokenService } from "../token/token.service";
import { Observable } from "rxjs";
import { PF_API } from "pf.api"; 
import { ColumnData1Model } from "./charts/models/column-data1.model";
import { PieData1Model } from "./charts/models/pie-data1.model";
import { PredictionDataDashboardModel } from "./charts/models/predictions-dashboard-data.model";

@Injectable()
export class DashboardService{

    constructor(private http: HttpClient,
                private tokenService: TokenService){}
    
    getValuesForBarChart(): Observable<ColumnData1Model[]>{
        return this.http.get<ColumnData1Model[]>(`${PF_API}/dashboard/barchart`);
    }

    getValuesForPieChart(): Observable<PieData1Model[]>{
        return this.http.get<PieData1Model[]>(`${PF_API}/dashboard/piechart`);
    }

    getValuesForPieChartRecept(): Observable<PieData1Model[]>{
        return this.http.get<PieData1Model[]>(`${PF_API}/dashboard/piechartrecepts`);
    }

    getPredictionsVcto(): Observable<PredictionDataDashboardModel[]>{
        return this.http.get<PredictionDataDashboardModel[]>(`${PF_API}/dashboard/allpredvcdo`);
    }

    getValuesForPieChartExpensesCategory(): Observable<PieData1Model[]>{
        return this.http.get<PieData1Model[]>(`${PF_API}/dashboard/piechartcategoryexp`);
    }

    getValuesForPieChartReceptCategory(): Observable<PieData1Model[]>{
        return this.http.get<PieData1Model[]>(`${PF_API}/dashboard/piechartcategoryrecp`);
    }
}