import { Component, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ProviderService } from '../provider/provider.service';
import { CategoryService } from '../category/category.service';
import { ProviderModel } from '../provider/provider.model';
import { SubCategoryModel } from '../category/sub-category.model';
import { PredictionService } from './prediction.service';
import { PlotsModel } from './plots.model';
import { PredictionModel } from './prediction.model';
import { AccountService } from 'app/account/account.service';
import { AccountModel } from 'app/account/account.model';


declare var $: any;

@Component({
  selector: 'app-prediction',
  templateUrl: './prediction.component.html',
  styleUrls: ['./prediction.component.css']
})
export class PredictionComponent implements OnInit {

  formCadPreLaunches: FormGroup;

  accounts: AccountModel[];

  providers: ProviderModel[];

  subCategories: SubCategoryModel[];

  subCatFiltered: SubCategoryModel[] = [];

  parcelar: boolean = false;

  nroParcelas: number = 0;

  parcelas: PlotsModel[];

  return: string[];

  provider: ProviderModel;

  flag: number = 1;

  idChange: number = null;

  launcheChange: PredictionModel;

  constructor(private formBuilder: FormBuilder, private providerService: ProviderService,
    private categoryServie: CategoryService, private predicitonLaService: PredictionService,
    private accountService: AccountService) { }

  ngOnInit() {

    this.getAllProviders();

    this.getAllSubCategories();

    this.getAllAccounts();

    this.formCadPreLaunches = this.formBuilder.group({
      provider: this.formBuilder.group({
        id_provider: '',
      }),
      subCategory: this.formBuilder.group({
        id_subCategory: '',
      }),
      account: this.formBuilder.group({
        id_account: '',
      }),
      typeOfLaunch: this.formBuilder.control('S', [Validators.required]),
      value: this.formBuilder.control('', [Validators.required]),
      description: '',
      dt_exp: this.formBuilder.control('', [Validators.required]),
    })

    this.changeFlag();

    this.parcelas = this.predicitonLaService.getAllPlots();

  }

  setSubcategoryByProvider() {
    this.provider = this.findProviderById(this.formCadPreLaunches.get('provider').value.id_provider);
    this.formCadPreLaunches.get('subCategory').patchValue({
      id_subCategory: this.provider.subCategory.id_subCategory,
    })
    this.formCadPreLaunches.patchValue({
      typeOfLaunch: this.provider.category.typeOfLaunch,
    })
  }


  getAllProviders() {
    this.providerService.getAllProviders().subscribe(providers => this.providers = providers)
  }

  getAllSubCategories() {
    this.categoryServie.getAllSubCategory().subscribe(subCategories => this.subCategories = subCategories)
  }

  getAllAccounts() {
    this.accountService.getAllAccounts().subscribe(accounts => this.accounts = accounts);
  }

  isParcelar() {
    if (this.parcelar == false) {
      this.parcelar = true;
    } else {
      this.parcelar = false;
      this.predicitonLaService.renewPlots();
      this.parcelas = this.predicitonLaService.getAllPlots();
    }
  }

  save(prediction?: PredictionModel) {

    if (this.flag === 1) {
      if (this.predicitonLaService.getAllPlots().length == 0) {
        this.predicitonLaService.savePredLaunche(prediction)
          .subscribe((prediction: PredictionModel) => {
            this.showNotification("Programo efetuada com sucesso", "success", "ti-face-smile");
            this.formCadPreLaunches.reset();
          })
      } else {
        for (var i = 0; i < this.parcelas.length; i++) {
          var pred = new PredictionModel(null, i + 1, prediction.subCategory, prediction.typeOfLaunch, prediction.provider, this.parcelas[i].value,
            prediction.description, this.parcelas[i].vcto, 0, prediction.account);
          this.predicitonLaService.pushPrediction(pred);
          console.log(pred.value);
        }
        this.predicitonLaService.saveCollePredLaunche(this.predicitonLaService.getPredictions())
          .subscribe((preds: PredictionModel[]) => {
            this.showNotification("Programes efetuadas com sucesso", "success", "ti-face-smile");
            this.resetForm();
          })
      }
    } else {
      prediction.id_launchPrediction = this.idChange;
      this.predicitonLaService.putLaunches(prediction)
        .subscribe((response: PredictionModel) => {
          this.showNotification('Programao atualizada com sucesso', "success", "ti-face-smile")
          this.formCadPreLaunches.reset()
          this.flag = 1
        })
    }
  }

  findProviderById(id: number): ProviderModel {
    for (var i = 0; i < this.providers.length; i++) {
      if (this.providers[i].id_provider == id) {
        return this.providers[i];
      }
    }
    return null;
  }

  setParcelas(parc: number, value: number, data: string): void {
    // value = this.setValue(value.toString());
    if (parc <= 1) {
      this.showNotification("O nmero de parcelas dever ser maior que 1", "warning", "ti-face-sad");
    } else {
      console.log(value);
      this.nroParcelas = parc;
      this.return = data.split('-');
      this.predicitonLaService.renewPlots();
      for (var i = 0; i < this.nroParcelas; i++) {
        var date2 = new Date();
        date2.setFullYear(Number(this.return[0]));
        date2.setMonth(Number(this.return[1]) - 1 + i);
        date2.setDate(Number(this.return[2]));
        var plot = new PlotsModel(i, value / parc, date2);
        this.predicitonLaService.pushPlot(plot);
      }
      this.parcelas = this.predicitonLaService.getAllPlots();
    }
  }

  setValue(value: string): number {
    var a = value.split('.');
    var b = '';
    if (a.length > 0 && a !== undefined){
      a.forEach(element => {
        b = b+element;
      });
    }
    a = null
    a = b.split(',');
    if (a.length >0 && a[1] !== undefined){
      b = '';
      b = a[0]+'.'+a[1]
    }
    return Number(b);
  }

  changeFlag(): void {
    if (this.predicitonLaService.getLaunc() != null) {
      this.flag = 0;
      this.launcheChange = this.predicitonLaService.getLaunc();
      this.idChange = this.launcheChange.id_launchPrediction;
      var date2 = new Date(this.launcheChange.dt_exp)
      this.formCadPreLaunches.setValue({
        provider: {
          id_provider: this.launcheChange.provider.id_provider,
        },
        subCategory: {
          id_subCategory: this.launcheChange.subCategory.id_subCategory,
        },
        account: {
          id_account: this.launcheChange.account.id_account,
        },
        typeOfLaunch: this.launcheChange.typeOfLaunch,
        value: this.launcheChange.value,
        description: this.launcheChange.description,
        dt_exp: date2.toISOString().substring(0, 10),
      })
    }
  }

  showNotification(message: string, type: string, icon: string) {
    $.notify({
      icon: icon,
      message: message
    }, {
        type: type,
        timer: 1500,
        placement: {
          from: "top",
          align: 'center',
        }
      });
  }

  resetForm() {
    this.parcelas = [];
    this.nroParcelas = 0;
    this.parcelar = false;
    this.changeFlag();
    this.formCadPreLaunches.reset();
    this.predicitonLaService.renewPlots();
    this.predicitonLaService.renewPredictions();
  }

  subCatFilter(filter: string) {
    this.subCatFiltered = [];
    this.subCategories.forEach(element => {
      if (element.category.typeOfLaunch === filter){
        this.subCatFiltered.push(element)
      }
    });
  }

  delPlots(id: number) {
    this.predicitonLaService.delPlotById(id);

    if (this.predicitonLaService.getPlotsLenght() <= 1) {
      this.nroParcelas = 0;
      this.parcelar = false;
    } else {
      this.nroParcelas = this.predicitonLaService.getPlotsLenght();
      this.setParcelas(this.predicitonLaService.getPlotsLenght(), this.formCadPreLaunches.get('value').value, this.formCadPreLaunches.get('dt_exp').value);
    }
  }
}
