import { Component, OnInit, Output } from '@angular/core';
import { TokenService } from '../token/token.service';
import { AccountService } from '../account/account.service';
import { AccountModel } from '../account/account.model';
import { Router } from '@angular/router';
import { DashboardService } from './dashboard.service';
import { ColumnData1Model } from './charts/models/column-data1.model';
import { Chart } from 'chart.js';
import { PieData1Model } from './charts/models/pie-data1.model';
import { PredictionDataDashboardModel } from './charts/models/predictions-dashboard-data.model';

declare var $:any;

@Component({
    selector: 'dashboard-cmp',
    moduleId: module.id,
    templateUrl: 'dashboard.component.html'
})
export class DashboardComponent implements OnInit{

  chart = [];

  pieChartData: PieData1Model[];
  pieChartData2: PieData1Model[];
  pieChartData3: PieData1Model[];
  pieChartData4: PieData1Model[];
  labels: string[] = [];
  expenses: number[] = [];
  recipe: number[] = [];

  valueVctoTable: PredictionDataDashboardModel[] = [];

  datapie: number[] = [];
  labelsPie: string[] = [];
  pieColor: string[] = [];

  datapie2: number[] =  [];
  labelsPie2: string[] = [];
  pieColor2: string[] = [];

  datapie3: number[] =  [];
  labelsPie3: string[] = [];
  pieColor3: string[] = [];

  datapie4: number[] =  [];
  labelsPie4: string[] = [];
  pieColor4: string[] = [];

  sumEntrada: number = 0;
  sumSaida: number = 0;

  @Output() chartBarValues: ColumnData1Model[];

  @Output() accounts: AccountModel[] = [];

    constructor(private tokenService: TokenService,
              private accountService: AccountService, 
              private router: Router,
              private dashboardSerice: DashboardService,
    ){}


    ngOnInit(){

      this.gelAllAccounts();

      this.setValuesOfChartbar();

      this.setValuesOfPieChart2();
      
      this.setValuesOfPieChart();

      this.setValuesOfVctoPredTable();

      this.setValuesOfPieChart3();

      this.setValuesOfPieChart4();

    }

    gelAllAccounts(){
      this.accountService.getAllAccounts().subscribe((accs: AccountModel[]) => {
        this.accounts = accs;
      });
    }

    editAccount(acc: AccountModel){
      this.accountService.accountpass = acc;
      this.router.navigate(['cadaccount']);
    }

    barIsEmpty(): boolean {
      if ((this.expenses !== null && this.expenses.length !== 0) || (this.recipe !== null && this.recipe.length !== 0)){
        return false;
      } else {
        return true;
      }
    }

    datapie2IsEmpty(): boolean{
      if (this.datapie2 !== null && this.datapie2.length !== 0){
        return false;
      } else{
      return true;
      }
    }

    datavalueVctoTableisEmpty(): boolean{
      if (this.valueVctoTable !== null && this.valueVctoTable.length !== 0){
        return false;
      } else {
        return true;
      }
    }

    datapieIsEmpty(): boolean{
      if(this.datapie.length !== 0 && this.datapie !== null){
        return false;
      } else {
          return true;
      }
    }

    databarChartIsEmpty(): boolean{
      if (this.recipe.length===0){
        return true;
      } else{
        return false;
      }
    }

    dataAccountsIsEmpty(): boolean{
      if (this.accounts.length === 0){
        return true;
      } else {
        return false;
      }
    }

    setValuesOfChartbar(){
      this.dashboardSerice.getValuesForBarChart().subscribe((value: ColumnData1Model[]) =>{
        this.chartBarValues = value;
        this.valuesOfBarChart();
        this.drawChartBar();
      })
    }

    setValuesOfPieChart(){
      this.dashboardSerice.getValuesForPieChart().subscribe((value: PieData1Model[]) => {
        this.pieChartData = value;
        this.valuesOfPieChart();
        this.drawChartPie();
      })
    }

    setValuesOfVctoPredTable(){
      this.dashboardSerice.getPredictionsVcto().subscribe((value: PredictionDataDashboardModel[]) =>{
        this.valueVctoTable = value;
        this.sumValues();
      })
    }

    setValuesOfPieChart2(){
      this.dashboardSerice.getValuesForPieChartRecept().subscribe((value: PieData1Model[]) => {
        this.pieChartData2 = value;
        this.valuesOfPieChart2();
        this.drawChartPieRecept();
      })
    }

    setValuesOfPieChart3(){
      this.dashboardSerice.getValuesForPieChartExpensesCategory().subscribe((value: PieData1Model[]) => {
        this.pieChartData3 = value;
        this.valuesOfPieChart3();
        this.drawChartPieExpCategory();
      })
    }

    setValuesOfPieChart4(){
      this.dashboardSerice.getValuesForPieChartReceptCategory().subscribe((value: PieData1Model[]) => {
        this.pieChartData4 = value;
        this.valuesOfPieChart4();
        this.drawChartPieReceCategory();
      })
    }

    valuesOfPieChart(): void{
      for (var i=0; i<this.pieChartData.length; i++){
        this.labelsPie.push(this.pieChartData[i].subCategory);
        this.datapie.push(this.pieChartData[i].value);
        this.pieColor.push(this.pieChartData[i].color);
      }
    }

    valuesOfPieChart2(): void{
      for (var i=0; i<this.pieChartData2.length; i++){
        this.labelsPie2.push(this.pieChartData2[i].subCategory);
        this.datapie2.push(this.pieChartData2[i].value);
        this.pieColor2.push(this.pieChartData2[i].color);
      }
    }

    valuesOfPieChart3(): void{
      for (var i=0; i<this.pieChartData3.length; i++){
        this.labelsPie3.push(this.pieChartData3[i].subCategory);
        this.datapie3.push(this.pieChartData3[i].value);
        this.pieColor3.push(this.pieChartData3[i].color);
      }
    }

    valuesOfPieChart4(): void{
      for (var i=0; i<this.pieChartData4.length; i++){
        this.labelsPie4.push(this.pieChartData4[i].subCategory);
        this.datapie4.push(this.pieChartData4[i].value);
        this.pieColor4.push(this.pieChartData4[i].color);
      }
    }

    valuesOfBarChart(): void{
      for (var i: number = 0; i<this.chartBarValues.length ; i++){
        this.labels.push(this.chartBarValues[i].date);
        if (this.chartBarValues[i].expenses !== null){
          this.expenses.push(this.chartBarValues[i].expenses);
        }
        if (this.chartBarValues[i].recipe !== null){
          this.recipe.push(this.chartBarValues[i].recipe);
        }
      }
    }

    sumValues(){
      for( var i = 0; i<this.valueVctoTable.length; i++){
        if(this.valueVctoTable[i].typeOfLaunch == 'C'){
          this.sumEntrada = this.sumEntrada+this.valueVctoTable[i].value;
        } else if (this.valueVctoTable[i].typeOfLaunch == 'S'){
          this.sumSaida = this.sumSaida+this.valueVctoTable[i].value;
        }
      }
    }

    drawChartBar(): void{
      this.chart = new Chart('barChart', {
        type: 'bar',
        data: {
          labels: this.labels,
          datasets: [
            {
              label: 'Receitas',
              data: this.recipe,
              backgroundColor: 'rgba(102, 102, 255, 0.7)',
            }, 
            {
              label: 'Despesas',
              data: this.expenses,
              backgroundColor: 'rgba(255, 102, 102, 0.7)',
            } 
        ],
        },
        options: {
          tooltips: {
              callbacks: {
                  label: function (t, d) {
                      var xLabel = d.datasets[t.datasetIndex].label;
                      var yLabel = t.yLabel >= 1000 ? '$' + t.yLabel.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") : '$' + t.yLabel;
                      return xLabel + ': ' + yLabel;
                  }
              }
          },
          legend: {
              display: true,
              position: 'bottom',
          },
          scales: {
              yAxes: [{
                  ticks: {
                      beginAtZero: true,
                      callback: function (value, index, values) {
                          if (parseInt(value) >= 1000) {
                              return '$' + value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
                          } else { return '$' + value; }
                      }
                  }
              }]
          }
      },
      plugins: [{
          beforeDraw: function (chart) {
              var labels = chart.data.labels;
          }
      }]
      });
    }

    drawChartPie(): void{
      this.chart = new Chart('doughnutChart',{
        type: 'doughnut',
        data: {
          datasets: [{
            data:this.datapie,
            label: 'Dataset1',
            backgroundColor: this.pieColor,
          },
        ],
          labels: this.labelsPie,
        },
        options: {
          legend: {
            display: true,
            position: 'bottom',
          },
        }
      })
    }

    drawChartPieRecept(): void{
      this.chart = new Chart('doughnutChart2',{
        type: 'doughnut',
        data: {
          datasets: [{
            data:this.datapie2,
            label: 'Dataset2',
            backgroundColor: this.pieColor2,
          },
        ],
          labels: this.labelsPie2,
        },
        options: {
          legend: {
            display: true,
            position: 'bottom',
          },
        }
      })
    }

    drawChartPieExpCategory(): void{
      this.chart = new Chart('doughnutChart3',{
        type: 'polarArea',
        data: {
          datasets: [{
            data:this.datapie3,
            label: 'Dataset3',
            backgroundColor: this.pieColor3,
          },
        ],
          labels: this.labelsPie3,
        },
        options: {
          cutoutPercentage: 0,
          legend: {
            display: true,
            position: 'bottom',
          },
        }
      })
    }

    drawChartPieReceCategory(): void{
      this.chart = new Chart('doughnutChart4',{
        type: 'polarArea',
        data: {
          datasets: [{
            data:this.datapie4,
            label: 'Dataset4',
            backgroundColor: this.pieColor4,
          },
        ],
          labels: this.labelsPie4,
        },
        options: {
          cutoutPercentage: 0,
          legend: {
            display: true,
            position: 'bottom',
          },
        }
      })
    }
}
