import { Component, OnInit } from '@angular/core';
import { LaunchesService } from 'app/launches/launches.service';
import { AccountService } from 'app/account/account.service';
import { AccountModel } from 'app/account/account.model';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { LaunchesModel } from 'app/launches/launches.model';
import { TransferModel } from './transfer.model';

declare var $: any;

@Component({
  selector: 'app-trasnfer',
  templateUrl: './trasnfer.component.html',
  styleUrls: ['./trasnfer.component.css']
})
export class TrasnferComponent implements OnInit {

  accounts: AccountModel[] = [];

  formTransfer: FormGroup;

  accountC = new AccountModel(null, null, null, 0, null, null, null, null);

  accountD = new AccountModel(null, null, null, 0, null, null, null, null);

  credito = new LaunchesModel(null, null, null, this.accountC, null, null, 'C',
                              'Transferncia de valores - Entrada', 0, 0, 0, 0, 0);

  debito = new LaunchesModel(null, null, null, this.accountD, null, null, 'S',
    'Transferncia de valores - Sada', 0, 0, 0, 0, 0);

  transfer = new TransferModel(this.credito, this.debito);

  constructor(private launcheService: LaunchesService,
              private accountsService: AccountService,
              private formBuilder: FormBuilder
              ) { }

  ngOnInit() {

    this.getAccouts();

    this.formTransfer = this.formBuilder.group({
      debito: this.formBuilder.group({
        id_account: this.formBuilder.control('', [Validators.required]),
      }),
      credito: this.formBuilder.group({
        id_account: this.formBuilder.control('', [Validators.required]),
      }),
      descricao: this.formBuilder.control('', [Validators.required]),
      valor: this.formBuilder.control('', [Validators.required]),
      date: this.formBuilder.control('', [Validators.required])
    })
  }

  getAccouts(){
    this.accountsService.getAllAccounts().subscribe((response: AccountModel[]) => {
      this.accounts = response;
    })
  }

  saveTransfer(){
    console.log(this.formTransfer.get('credito').value);
    this.credito.account = this.formTransfer.get('credito').value;
    this.credito.description = this.formTransfer.get('descricao').value;
    this.credito.dt = this.formTransfer.get('date').value;
    this.credito.pay_value = this.formTransfer.get('valor').value;

    this.debito.account = this.formTransfer.get('debito').value;
    this.debito.description = this.formTransfer.get('descricao').value;
    this.debito.dt = this.formTransfer.get('date').value;
    this.debito.pay_value = this.formTransfer.get('valor').value;

    this.launcheService.transfer(this.transfer).subscribe((value: any) => {
      console.log('Tudo ok');
      this.showNotification("Transferncia executda com sucesso.", "success", "ti-face-smile");
      this.formTransfer.reset();
    })
  }

  resetForm() {
    this.formTransfer.reset();
  }

  showNotification(message: string, type: string, icon: string) {
    $.notify({
      icon: icon,
      message: message
    }, {
        type: type,
        timer: 1500,
        placement: {
          from: "top",
          align: 'center',
        }
      });
  }
}
