import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { UserModel } from '../users/user.model';
import { UserService } from '../users/user.service';
import { TokenService } from '../token/token.service';
import { SubCategoryPassModel } from '../category/sub-category-pass.model';

declare var $:any;

@Component({
    selector: 'user-cmp',
    moduleId: module.id,
    templateUrl: 'user.component.html'
})

export class UserComponent implements OnInit{

    formUserChange: FormGroup;

    changePassword: boolean = false;

    title: 'Usurio';

    user = new UserModel(null,'','', '', false, '', '', '', false);

    constructor(private userService: UserService, private formBuilder: FormBuilder,
                private tokenService: TokenService){}

    ngOnInit(){
        this.user = this.tokenService.user;

        this.formUserChange = this.formBuilder.group({
            name: this.formBuilder.control(this.user.name, [Validators.required, Validators.minLength(6)]),
            login: this.formBuilder.control(this.user.login, [Validators.required, Validators.minLength(5)]),
            email: this.formBuilder.control(this.user.email, [Validators.required, Validators.email]),
            password: this.formBuilder.control('', [Validators.required, Validators.minLength(6)]),
            passwordRpt: this.formBuilder.control('', [Validators.required, Validators.minLength(6)]),
            sex: this.formBuilder.control(this.user.sex, [Validators.required]),
        })
    }

    isChangePassword(): boolean{
        return this.changePassword;
    }

    setChangePassword(){
        if(this.changePassword){
            this.changePassword = false;
        } else {
            this.changePassword = true;
        }
    }

    saveCadUser(){
        if(this.isChangePassword()){
            this.user.id_usuario = this.tokenService.user.id_usuario;
            this.user.name = this.formUserChange.get("name").value;
            this.user.email = this.formUserChange.get("email").value;
            this.user.password = this.formUserChange.get("password").value;
            this.user.sex = this.formUserChange.get("sex").value;
            this.user.profileEnum = 'ROLE_CUSTOMER';
            this.userService.putUserFromCustomer(this.user).subscribe((user: UserModel) =>{
                this.showNotification("Seus dados e senha foram atualizados com sucesso.", "success", "ti-face-smile");
                this.user.password = '';
                this.tokenService.user = user;
                this.formUserChange.get('password').setValue('');
                this.formUserChange.get('passwordRpt').setValue('');
                this.changePassword = false;
            })
        } else {
            this.user.id_usuario = this.tokenService.user.id_usuario;
            this.user.name = this.formUserChange.get("name").value;
            this.user.email = this.formUserChange.get("email").value;
            this.user.password = null;
            this.user.profileEnum = 'ROLE_CUSTOMER';
            this.user.sex = this.formUserChange.get("sex").value;
            this.userService.putUserFromCustomer(this.user).subscribe((user: UserModel) =>{
                this.showNotification("Seus dados foram atualizados com sucesso.", "success", "ti-face-smile");
                this.user.password = '';
                this.tokenService.user = user;
                this.formUserChange.get('password').setValue('');
                this.formUserChange.get('passwordRpt').setValue('');
                this.changePassword = false;
            })
        }
    }

    showNotification(message: string, type: string, icon: string) {
        $.notify({
          icon: icon,
          message: message
        }, {
            type: type,
            timer: 1500,
            placement: {
              from: "top",
              align: 'center',
            }
          });
      }
}
