import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '../../../node_modules/@angular/forms';
import { UserModel } from 'app/users/user.model';
import { UserService } from 'app/users/user.service';

declare var $:any;

@Component({
  selector: 'app-cad-user-nloged',
  templateUrl: './cad-user-nloged.component.html',
  styleUrls: ['./cad-user-nloged.component.css']
})
export class CadUserNlogedComponent implements OnInit {

  formNewUserCad: FormGroup;

  passUser: UserModel;

  constructor(private userService: UserService, private formBuilder: FormBuilder) { }

  ngOnInit() {

    this.formNewUserCad = this.formBuilder.group({
      name: this.formBuilder.control('', [Validators.required, Validators.minLength(6)]),
      login: this.formBuilder.control('', [Validators.required, Validators.minLength(5)]),
      password: this.formBuilder.control('', [Validators.required, Validators.minLength(6)]),
      passwordRpt: this.formBuilder.control('', [Validators.required, Validators.minLength(6)]),
      sex: this.formBuilder.control('', [Validators.required]),
      email: this.formBuilder.control('', [Validators.email, Validators.required])
    });
  }


  saveUser(usr: UserModel){
    if (this.testaSenha() === true){
      this.userService.saveNewUsers(usr)
      .subscribe((user: UserModel) => {
        this.showNotification("Usurio cadastrado com sucesso", "success", "ti-face-smile")
        this.formNewUserCad.reset()
      })
    } else {
      this.showNotification("As senhas no coicidem!", "warning", "ti-info")
      this.formNewUserCad.get("password").reset();
      this.formNewUserCad.get("passwordRpt").reset();
    }
  }


  testaSenha(): boolean {
    if(this.formNewUserCad.get("password").value === this.formNewUserCad.get("passwordRpt").value){
      return true;
    } else
    return false;
  }

  showNotification(message: string, type: string, icon: string) {
    $.notify({
      icon: icon,
      message: message
    }, {
        type: type,
        timer: 1500,
        placement: {
          from: "top",
          align: 'center',
        }
      });
  }

  resetForm(){
    this.formNewUserCad.reset();
  }
}
