import { Injectable } from "@angular/core";
import { HttpClient, HttpRequest } from "@angular/common/http";
import { Observable } from "rxjs";
import { PF_API } from "pf.api";
import { PredictionModel } from "app/prediction/prediction.model";
import { PredictionPassModel } from "app/prediction/prediction.pass.model";

@Injectable()
export class PrevisonService{
    constructor(
        private http: HttpClient
    ){}

    public getAllPredictionByFilter(prediction: PredictionPassModel): Observable<PredictionModel[]>{
        return this.http.post<PredictionModel[]>(`${PF_API}/prediction/filter`, prediction);
    }


    public init():Observable<PredictionModel[]>{
        var pass = new PredictionPassModel(null, 0, null, null);
        return this.getAllPredictionByFilter(pass);
    }

    upLoadFile(file: File) {
        let formData = new FormData();
        formData.append('file', file);
        const req = new HttpRequest('POST', `${PF_API}/launches/upload2`, formData,{
            reportProgress: true,
            responseType: 'text'
        });
        return this.http.request(req);
    }
}